      RegionOnly: false,
      RegionRank: undefined,
      Support: MSP.Support.Yes,
      Statement:
        "If we are serious about wanting Scotland to be the best country in the world for LGBT+ people, then that means using the powers of the Scottish government to challenge hatred and discrimination, no matter where it comes from.  The End Conversion Therapy campaign have done valuable work keeping this on the political agenda by asking candidates in this Mays election to back their pledge. I would like to go one step further and commit that my party will back legislation for a ban on conversion therapy for LGBT+ people. This legislation would set in law sanctions for causing a person causing a person to undergo conversion therapy providing or advertising conversion therapy and importantly, removing a minor from Scotland to undergo conversion therapy abroad. I think that Scotland can be genuinely proud of the work that it has done to improve LGBT+ rights but there is plenty more to do. Thats why Im proud to pledge my partys support in ensuring this practice must be ended once and for all.",
